import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { ResolucionModel } from '../models/gestion-academica/resolucion.model';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';
import { PreguntaModel } from '../models/gestion-academica/pregunta.model';

@Injectable({
    providedIn: 'root'
})
export class PreguntaService {

    constructor(
        private http: HttpClient,
    ) { }

    public getPreguntasByTipo(tipoPreguntas: number[]): Observable<PreguntaModel[]> {
        const url = `${environment.backend.gestion_academica}api/pregunta/v1/get-by-tipo-preguntas`;
        return this.http.post<PreguntaModel[]>(url, tipoPreguntas);
    }

}