import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { TipoProductividadesModel } from '../models/gestion-academica/tipo-productividades.model';
@Injectable({
    providedIn: 'root'
})
export class TipoProductividadService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllTipoAcademico(): Observable<TipoProductividadesModel[]> {
        return this.http.get<TipoProductividadesModel[]>(`${environment.backend.gestion_academica}api/tipo-productividades/v1/get-all`);
    }

}