import { EvaluacionCompromisoModel } from './evaluacion-compromiso.model';
import { PreguntaModel } from './pregunta.model';

export class EvaluacionDetalleModel {

    public id: number = 0;
    public idPregunta: PreguntaModel = null;
    public idEvaluacion: EvaluacionCompromisoModel = null;
    public respuesta: string = "";

    constructor(fields?: {
        id?: number,
        idPregunta?: PreguntaModel,
        idEvaluacion?: EvaluacionCompromisoModel,
        respuesta?: string
    }) {
        if (fields) { Object.assign(this, fields); }
    }
}