import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { SearchPagination } from '../interfaces/search.pagination';
import { Observable } from 'rxjs';
import { ResolucionModel } from '../models/gestion-academica/resolucion.model';
import { IPaginacion } from '../interfaces/paginacion.interface';
import { environment } from 'src/environments/environment';
import { PreguntaVcmModel } from '../models/gestion-academica/pregunta-vcm.model';

@Injectable({
    providedIn: 'root'
})
export class PreguntaVcmService {

    constructor(
        private http: HttpClient,
    ) { }

    public getPreguntasByFuncion(funcion: number): Observable<PreguntaVcmModel[]> {
        const url = `${environment.backend.gestion_academica}api/pregunta-vcm/v1/get-by-funcion/${funcion}`;
        return this.http.get<PreguntaVcmModel[]>(url);
    }

}