  //Fin Funciones de Formulario
  calculoPorcentaje() {
    this.porcentajeInvestigacion = parseInt(this.formCompromiso.get("porcentajeInvestigacion").value);
    this.porcentajeTotal = parseInt(this.formCompromiso.get("porcentajeDocencia").value) + parseInt(this.formCompromiso.get("porcentajeGestion").value) + parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) + parseInt(this.formCompromiso.get("porcentajeVcm").value)
    this.calcularTrayectorias();
    if (this.porcentajeTotal >= 0 && this.porcentajeTotal < 100) {
      this.progress = "#FF8800"
    } else if (this.porcentajeTotal == 100) {
      this.progress = "#5cb85c"
    } else if (this.porcentajeTotal > 100) {
      this.progress = "#FF4444"
    }

  }

  calcularTrayectorias() {
    this.funciones = 0;
    this.porcentajesBuenos = true;
    const docencia = parseInt(this.formCompromiso.get("porcentajeDocencia").value)
    const gestion = parseInt(this.formCompromiso.get("porcentajeGestion").value)
    const investigacion = parseInt(this.formCompromiso.get("porcentajeInvestigacion").value)
    const vcm = parseInt(this.formCompromiso.get("porcentajeVcm").value)

    this.docencia = docencia;
    this.gestion = gestion;
    this.investigacion = investigacion;
    this.vcm = vcm;

    if (((docencia + gestion + investigacion + vcm) < 100 && (docencia + gestion + investigacion + vcm) > 0) || ((docencia + gestion + investigacion + vcm) > 100) || (docencia + gestion + investigacion + vcm) == 0) {
      if (docencia > gestion && docencia > investigacion && docencia > vcm) {
        this.trayectoria = 'Formacin';
        this.subtrayectoria = 'Formacin Exclusiva'
      } else if (gestion > docencia && gestion > investigacion && gestion > vcm) {
        this.trayectoria = 'Gestin';
        this.subtrayectoria = 'Gestin';
      } else if (investigacion > docencia && investigacion > gestion && investigacion > vcm) {
        this.trayectoria = 'Investigacin';
        this.subtrayectoria = 'Investigacin';
      } else if (docencia == gestion || docencia == vcm || docencia == investigacion || investigacion == gestion || investigacion == vcm || gestion == vcm) {
        this.trayectoria = 'Armnica';
        this.subtrayectoria = 'Armnica';
      } else if (docencia == gestion && investigacion == vcm && docencia == vcm) {
        this.trayectoria = 'Armnica'
        this.subtrayectoria = 'Armnica 4 Funciones'
      } else {
        this.trayectoria = 'Armnica';
        this.subtrayectoria = 'Armnica 3 Funciones'
      }
      this.porcentajesBuenos = true;
    } else if (docencia == 100) {
      this.subtrayectoria = "Formacin Exclusiva"
      this.trayectoria = "Formacin"
    } else if (gestion == 100) {
      this.subtrayectoria = "Gestion"
      this.trayectoria = "Gestin"
    } else if (docencia >= 50 && investigacion > 0 && vcm == 0 && gestion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Formacin Investigacin"
      this.trayectoria = "Formacin"
    } else if (docencia >= 50 && gestion > 0 && investigacion > 0 && vcm == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Formacin Gestin Investigacin"
      this.trayectoria = "Formacin"
    } else if (docencia >= 50 && gestion > 0 && vcm == 0 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Formacin Gestin"
      this.trayectoria = "Formacin"
    } else if (docencia >= 50 && gestion > 0 && vcm > 0 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Formacin Gestin Vcm"
      this.trayectoria = "Formacin"
    } else if (docencia >= 50 && investigacion > 0 && vcm > 0 && gestion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Formacin Investigacin Vcm"
      this.trayectoria = "Formacin"
    } else if (docencia >= 20 && gestion >= 50 && vcm == 0 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Gestin Formacin A"
      this.trayectoria = "Gestin"
    } else if (docencia < 20 && docencia > 0 && gestion >= 50 && vcm > 0 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Gestin Formacin B"
      this.trayectoria = "Gestin"
    } else if (docencia > 0 && gestion >= 50 && vcm > 0 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Gestin Formacin Vcm"
      this.trayectoria = "Gestin"
    } else if (docencia >= 50 && gestion == 0 && vcm > 0 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Formacin Vcm"
      this.trayectoria = "Formacin"
    } else if (docencia >= 20 && gestion == 0 && vcm == 0 && investigacion >= 50 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Investigacin Formacin A"
      this.trayectoria = "Investigacin"
    } else if (docencia > 0 && gestion == 0 && vcm == 0 && investigacion >= 50 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Investigacin Formacin B"
      this.trayectoria = "Investigacin"
    } else if (docencia <= 20 && gestion > 50 && vcm == 0 && investigacion <= 20 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Gestin Formacin Investigacin"
      this.trayectoria = "Gestin"
    } else if (docencia > 0 && gestion > 0 && vcm == 0 && investigacion >= 50 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Investigacin Formacin Gestin"
      this.trayectoria = "Investigacin"
    } else if (docencia > 0 && gestion == 0 && vcm > 0 && investigacion >= 50 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Investigacin Formacin Vcm"
      this.trayectoria = "Investigacin"
    } else if (docencia >= 26 && gestion == 0 && vcm >= 26 && investigacion >= 26 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Armnica 3 Funciones D-I-VcM"
      this.trayectoria = "Armnica"
    } else if (docencia >= 26 && gestion >= 26 && vcm >= 26 && investigacion == 0 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Armnica 3 Funciones D-VcM-G"
      this.trayectoria = "Armnica"
    } else if (docencia >= 26 && gestion >= 26 && vcm == 0 && investigacion >= 26 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Armnica 3 Funciones D-I-G"
      this.trayectoria = "Armnica"
    } else if (docencia == 0 && gestion >= 26 && vcm >= 26 && investigacion >= 26 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Armnica 3 Funciones I-VcM-G"
      this.trayectoria = "Armnica"
    } else if (docencia >= 20 && gestion >= 20 && vcm >= 20 && investigacion >= 20 && this.porcentajeTotal == 100) {
      this.subtrayectoria = "Armnica 4 Funciones"
      this.trayectoria = "Armnica"
    } else {
      if (docencia > gestion && docencia > investigacion && docencia > vcm) {
        this.trayectoria = 'Formacin';
        this.subtrayectoria = 'Formacin Exclusiva'
      } else if (gestion > docencia && gestion > investigacion && gestion > vcm) {
        this.trayectoria = 'Gestin';
        this.subtrayectoria = 'Gestin'
      } else if (investigacion > docencia && investigacion > gestion && investigacion > vcm) {
        this.trayectoria = 'Investigacin';
        this.subtrayectoria = 'Investigacin'
      } else if (docencia == gestion || docencia == vcm || docencia == investigacion || investigacion == gestion || investigacion == vcm || gestion == vcm) {
        this.trayectoria = 'Armnica';
        this.subtrayectoria = 'Armnica';
      } else if (docencia == gestion && investigacion == vcm && docencia == vcm) {
        this.trayectoria = 'Armnica'
        this.subtrayectoria = 'Armnica 4 Funciones'
      } else {
        this.trayectoria = 'Armnica';
        this.subtrayectoria = 'Armnica 3 Funciones'
      }
    }

    this.obtenerTrayectoria();
    this.obtenerSubtrayectoria();

    if (docencia > 0) {
      this.funciones += 1
    }
    if (investigacion > 0) {
      this.funciones += 1
    }
    if (vcm > 0) {
      this.funciones += 1
    }
    if (gestion > 0) {
      this.funciones += 1
    }

    if (this.funciones > 1) {
      this.tipo = "Ordinario"
    } else {
      this.tipo = "Adjunto"
    }
    this.calcularDistribucionJornada();
  }

