  calcularIndicadorPublicaciones() {
    var facultadId: string = this.compromiso.rutDocente.facultad.codigo;
    var tipoAcademico: string = 'ACAIN';


    this.indicadorPublicacionesService.findByFacultadAndTipoAcademico(facultadId, tipoAcademico).subscribe((value) => {
      this.indicadorPublicaciones = value.numeroPublicaciones;
      this.indicadorAdjudicaciones = value.adjudicacionesAnual;
      const casoEspecial = this.filtrarPublicacion()
      if (casoEspecial != undefined) {
        this.indicadorPublicacionesEspeciales = this.indicadorPublicaciones * casoEspecial.valor
        this.ejemploPublicaciones = (this.indicadorPublicacionesEspeciales * 100) / 80
      } else {
        this.ejemploPublicaciones = (this.indicadorPublicaciones * 100) / 80
      }
      this.calculoPublicaciones();
      this.calcularCompromisoPublicaciones();
      this.calcularIndicadorLibros();
      this.calcularIndicadorCapitulos();


    });
  }

