  calcularHorasJornada() {

    if (this.compromiso.rutDocente.jornada == 'Completa') {
      this.horasJornada = 45;
      this.horasDedicadasInvestigacion = this.horasJornada * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) * 0.01
      this.modulosDedicadosInvestigacion = (this.horasDedicadasInvestigacion * 60) / 75
    } else if (this.compromiso.rutDocente.jornada == '3/4 Jornada') {
      this.horasJornada = 33.75
      this.horasDedicadasInvestigacion = this.horasJornada * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) * 0.01
      this.modulosDedicadosInvestigacion = (this.horasDedicadasInvestigacion * 60) / 75

    } else if (this.compromiso.rutDocente.jornada == '1/2 Jornada') {
      this.horasJornada = 22.5;
      this.horasDedicadasInvestigacion = this.horasJornada * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) * 0.01
      this.modulosDedicadosInvestigacion = (this.horasDedicadasInvestigacion * 60) / 75
    } else if (this.compromiso.rutDocente.jornada == 'Menos de 1/2 Jornada') {
      this.horasJornada = 11.25;
      this.horasDedicadasInvestigacion = this.horasJornada * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) * 0.01
      this.modulosDedicadosInvestigacion = (this.horasDedicadasInvestigacion * 60) / 75
    } else {
      this.horasJornada = 0;

    }

  }

  calcularAniosAdjudicacion() { //EJEMPLO (BP)
    if (this.trayectoria == "Investigacin") {
      this.aniosAdjudicacion = ((((this.modulosDedicadosInvestigacion * 2) / this.modulosDedicadosInvestigacion) * 100) / 80)
    } else {
      this.aniosAdjudicacion = 0
    }

    if (this.aniosAdjudicacion < 0.5 && this.aniosAdjudicacion > 0) {
      this.aniosCalculo = 1 / this.aniosAdjudicacion;
    } else {
      this.aniosCalculo = this.aniosAdjudicacion;
    }
    this.calculoAdjudicaciones()
  }

  calcularCompromisoAdjudicacion() {
    if ((parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) * 0.01) >= 0.7) {
      if (this.trayectoria == "Investigacin") {
        this.cantidadAdjudicacion = 1;
        if (Math.round(this.adjudicaciones) == 1) {
          this.stringAdjudicaciones = "1 Adjudicacin cada " + Math.round(this.adjudicaciones) + " ao"
          this.periodoAdjudicaciones = Math.round(this.adjudicaciones) - 1;
        } else {
          this.stringAdjudicaciones = "1 Adjudicacin cada " + Math.round(this.adjudicaciones) + " aos"
          this.periodoAdjudicaciones = Math.round(this.adjudicaciones) - 1;

        }
      } else {
        this.cantidadAdjudicacion = 0;
        this.periodoAdjudicaciones = 0;
        this.stringAdjudicaciones = "No Aplica";
      }
    } else {
      this.periodoAdjudicaciones = 0;
      this.cantidadAdjudicacion = 0;
      this.stringAdjudicaciones = "No Aplica"
    }
  }

  calculoAdjudicaciones() {
    if (this.compromiso.rutDocente.jornada == 'Completa') {
      this.adjudicaciones = this.aniosAdjudicacion * (parseInt(this.formCompromiso.get("porcentajeInvestigacion").value) * 0.01)
    } else if (this.compromiso.rutDocente.jornada == '3/4 Jornada') {
      this.adjudicaciones = (this.aniosAdjudicacion * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value)) / 0.75 * 0.01
    } else if (this.compromiso.rutDocente.jornada == '1/2 Jornada') {
      this.adjudicaciones = (this.aniosAdjudicacion * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value)) / 0.5 * 0.01
    } else if (this.compromiso.rutDocente.jornada == 'Menos de 1/2 Jornada') {
      this.adjudicaciones = (this.aniosAdjudicacion * parseInt(this.formCompromiso.get("porcentajeInvestigacion").value)) / 0.25 * 0.01
    } else {
      this.adjudicaciones = 0
    }

    if (this.adjudicaciones < 0.5 && this.adjudicaciones != 0) {
      this.adjudicaciones = 1 / this.adjudicaciones
    }
  }

