import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { CasosEspecialesModel } from '../models/gestion-academica/casos-especiales.model';
@Injectable({
    providedIn: 'root'
})
export class CasosEspecialesService {

    constructor(
        private http: HttpClient,
    ) { }

    public getAllCasosEspeciales(): Observable<CasosEspecialesModel[]> {
        return this.http.get<CasosEspecialesModel[]>(`${environment.backend.gestion_academica}api/casos-especiales/v1/get-all`);
    }

}