import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngrx/store';
import { max } from 'moment';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { CompromisoFormComponent } from 'src/app/paginas/compromiso-docente/compromiso-form/compromiso-form.component';
import { AppState } from 'src/app/store/app.reducer';
import Swal from 'sweetalert2';
import { CompromisoModel } from '../../models/gestion-academica/compromiso.model';
import { RequerimientosEspecialesModel } from '../../models/gestion-academica/requerimientos-especiales.model';
import { TipoProductividadesModel } from '../../models/gestion-academica/tipo-productividades.model';
import { ArchivoEvidenciaService } from '../../services/archivo-evidencia.service';
import { CalificacionService } from '../../services/calificacion.service';
import { TipoProductividadService } from '../../services/tipo-productividad.service';

import { UtilAlert } from '../../util/util.alert';
import { UtilValidation } from '../../util/util.validation';

@Component({
  selector: 'app-modal-requerimieno',
  templateUrl: './modal-requerimiento.component.html',
  styles: [
  ]
})
export class ModalRequerimientoComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;

  @ViewChild('modalRequerimiento', { static: false })
  modalRequerimiento: ElementRef;

  TipoProductividades: TipoProductividadesModel[] = []
  formRequerimiento: FormGroup;
  requerimiento: RequerimientosEspecialesModel
  listadoRequerimientosEspeciales: RequerimientosEspecialesModel[] = []
  placeholder: String = "";




  constructor(
    private modalParaRequerimiento: NgbModal,
    private store: Store<AppState>,
    private utilValidation: UtilValidation,
    public alertService: UtilAlert,
    private toastr: ToastrService,
    private calificacionService: CalificacionService,
    private archivoService: ArchivoEvidenciaService,
    private router: Router,
    private tipoProductividadService: TipoProductividadService,
    private compromisosForm: CompromisoFormComponent,
  ) { }

  ngOnInit(): void {
    this.initForm();
    this.tipoProductividadService.getAllTipoAcademico().subscribe((value) => {
      this.TipoProductividades = value
    });
  }

  initForm() {
    this.formRequerimiento = new FormGroup({
      tipoProductividad: new FormControl(0, [Validators.required]),
      cantidad: new FormControl(0, [Validators.required, Validators.min(0)]),
      periodo: new FormControl('', [Validators.required]),
      tipo: new FormControl('', [Validators.required]),
    })
    this.formRequerimiento.get("tipoProductividad").disable()
  }
  reiniciarModal() {
    this.formRequerimiento.get("tipoProductividad").setValue(0);
    this.formRequerimiento.get("cantidad").setValue(0);
    this.formRequerimiento.get("periodo").setValue("");
    this.formRequerimiento.get("tipo").setValue("");
    this.formRequerimiento.markAsUntouched();
    this.listadoRequerimientosEspeciales = [];
  }

  insertar() {
    this.requerimiento = new RequerimientosEspecialesModel();
    if (this.listadoRequerimientosEspeciales.length > 0) {
      let index: number = 0;
      do {
        console.log(this.formRequerimiento.get("tipoProductividad").value)
        console.log(this.listadoRequerimientosEspeciales[index].idTipoProductividad.id)
        if (this.formRequerimiento.get("tipoProductividad").value == this.listadoRequerimientosEspeciales[index].idTipoProductividad.id) {
          return this.toastr.info("Ya existe un requerimiento especial para este tipo de productivad")
        }
        index++
      } while (index < this.listadoRequerimientosEspeciales.length);

    }
    UtilValidation.setFormForValidate(this.formRequerimiento);
    if (this.formRequerimiento.valid) {

      this.requerimiento.cantidad = this.formRequerimiento.get("cantidad").value;
      let tipoProductividad = new TipoProductividadesModel();
      tipoProductividad.id = this.formRequerimiento.get("tipoProductividad").value;
      this.requerimiento.idTipoProductividad = tipoProductividad
      this.requerimiento.periodo = this.formRequerimiento.get("periodo").value;
      this.requerimiento.tipo = this.formRequerimiento.get("tipo").value;
      this.compromisosForm.addRequerimientoEspecial(this.requerimiento);
      this.compromisosForm.separarRequerimientoPorTipo();
      this.cerrarModal();
    }
  }

  openModal(listaRequerimientosEspeciales: RequerimientosEspecialesModel[], idTipoRequerimiento: number) {
    this.listadoRequerimientosEspeciales = listaRequerimientosEspeciales;
    this.formRequerimiento.get("tipoProductividad").setValue(idTipoRequerimiento.toString());
    if (idTipoRequerimiento == 0) {
      this.placeholder = "Wos o Scopus"
    } else if (idTipoRequerimiento == 1) {
      this.placeholder = "Tipo de Adjudicacion"
    } else if (idTipoRequerimiento == 3) {
      this.placeholder = "Libro o Captulo"
    }
    this.modalParaRequerimiento.open(this.modalRequerimiento, { size: 'sm', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false));
  }





  cerrarModal(dimissAll = true) {
    this.reiniciarModal()
    if (dimissAll) this.modalParaRequerimiento.dismissAll();
  }
}