import { Component, OnInit, ElementRef, ViewChild, EventEmitter, Output, OnDestroy, Input } from '@angular/core';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngrx/store';
import { AppState } from 'src/app/store/app.reducer';
import { FormControl, FormGroup } from '@angular/forms';
import { EncuestaDocenteModel } from 'src/app/commons/models/gestion-academica/encuesta-docente.model';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import { DocenteService } from 'src/app/commons/services/docente.service';
import { MateriaModel } from 'src/app/commons/models/gestion-academica/materia.model';
import { CompromisoService } from 'src/app/commons/services/compromiso.service';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-form-encuesta',
  templateUrl: './form-encuesta.component.html',
  styleUrls: ['./form-encuesta.component.css']
})
export class FormEncuestaComponent implements OnInit {


  @ViewChild('contentEncuestaDocente', { static: false })
  contentEncuestaDocente: ElementRef;
  public listaPeriodos: String[];
  public datosMateria: MateriaModel;
  public formularioTopBar: FormGroup;
  public periodo: string;
  public codigoCurso: string;
  public rutDocente: string;
  public encuestaDocente: EncuestaDocenteModel[];
  public stringError: String;
  public listaNrc: String[];
  public encuestaDocenteFiltrada: EncuestaDocenteModel[];
  public contestaron: string;
  public inscritos: string;
  public downloadingEncuesta: boolean = false;
  public nrc: string = null;
  public _subscriptionEncuesta: Subscription;
  public habilitarBotonDescargaEncuesta = false;
  public periodoMinimo = 202275;
  public periodoNumero: number = 0;
  public esPostgrado = false;


  constructor(
    private modalService: NgbModal,
    private alert: UtilAlert,
    private store: Store<AppState>,
    private docenteService: DocenteService,
    private compromisoService: CompromisoService,
  ) { }

  ngOnInit() {
    this._subscriptionEncuesta = this.store.select('compromiso').subscribe(state => {

      this.downloadingEncuesta = state.downloadingEncuesta;

    });
  }

  open(materia: MateriaModel, rutDocente: string) {
    this.rutDocente = rutDocente
    this.datosMateria = materia;
    this.listaPeriodos = this.datosMateria.periodoAcademico.split(" - ");
    this.iniciarBusqueda();
    this.modalService.open(this.contentEncuestaDocente, { ariaLabelledBy: 'modal-basic-title', size: 'lg' }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false));
  }

  iniciarBusqueda() {
    this.formularioTopBar = new FormGroup({
      listaPeriodos: new FormControl(''),
      listaNrc: new FormControl(''),
    });
  }

  buscarEncuesta() {
    this.reiniciarVariables();
    this.periodo = this.formularioTopBar.get("listaPeriodos").value;
    this.periodoNumero = Number(this.periodo);
    if (this.periodo.includes("40") || this.periodo.includes("80")) {
      this.esPostgrado = true;
    }
    this.codigoCurso = this.datosMateria.codigoCurso;
    this.docenteService.findEncuestaDocente(this.rutDocente, this.periodo, this.codigoCurso).subscribe(value => {
      if (value == null) {
        this.stringError = 'No se ha encontrado una encuesta docente';
        return;
      }
      if (value != null || value.length >= 0) {
        this.encuestaDocente = value;
        this.obtenerListaNrc(this.encuestaDocente);
        if (Number(this.periodo) >= this.periodoMinimo) {
          this.habilitarBotonDescargaEncuesta = true;
        }
      } else {
        this.stringError = 'No se ha encontrado una encuesta docente';
        return;
      }
    })
  }

  obtenerListaNrc(encuestaDocente: EncuestaDocenteModel[]) {
    debugger
    let auxiliar = 0
    let listaNrc: String[] = [];
    do {
      if (listaNrc.indexOf(encuestaDocente[auxiliar].nrc) == -1) {
        listaNrc.push(encuestaDocente[auxiliar].nrc)
      }
      auxiliar++
    } while (auxiliar < encuestaDocente.length)
    this.listaNrc = listaNrc;
  }

  filtrarEncuesta() {
    this.nrc = this.formularioTopBar.get("listaNrc").value;
    this.encuestaDocenteFiltrada = this.encuestaDocente.filter(valor => valor.nrc == String(this.nrc));
    console.log(this.nrc)
    console.log(this.encuestaDocenteFiltrada)
    this.contestaron = this.encuestaDocenteFiltrada[0].contestaron;
    this.inscritos = this.encuestaDocenteFiltrada[0].inscritos;
  }

  reiniciarVariables() {
    this.encuestaDocenteFiltrada = [];
    this.esPostgrado = false;
    this.inscritos = undefined;
    this.listaNrc = []
    this.stringError = undefined;
    this.periodo = "";
    this.encuestaDocente = [];
    this.nrc = null;
    this.downloadingEncuesta = false;
    this.habilitarBotonDescargaEncuesta = false;
  }

  descargarEncuesta() {
    if (!this.downloadingEncuesta) {
      this.compromisoService.descargarEncuesta(this.codigoCurso, this.rutDocente, this.periodo, this.nrc);
    }
  }

  cerrarModal(dismisAll = true) {
    this.reiniciarVariables()
    if (dismisAll) {
      this.modalService.dismissAll();
    }
  }

}
