import { CalificacionModel } from '../gestion-academica/calificacion.model';
import { CompromisoModel } from '../gestion-academica/compromiso.model';

export class EvaluacionCompromisoModel {

    public idEvaluacion: number = 0;
    public idCompromiso: CompromisoModel = null;
    public fechaRegistro: Date = null;
    public usuarioEvaluador: string = "";
    public idCalificacion: CalificacionModel = null;
    public aspectosMejorar: string = "";
    public aspectosMejorarVcm: string = "";
    public aspectosDestacar: string = "";
    public aspectosDestacarVcm: string = "";

    constructor(fields?: {
        idEvaluacion?: number,
        idCompromiso?: CompromisoModel,
        fechaRegistro?: Date,
        usuarioEvaluador?: string,
        idCalificacion?: CalificacionModel,
        aspectosMejorar?: string,
        aspectosMejorarVcm?: string,
        aspectosDestacar?: string,
        aspectosDestacarVcm?: string,
    }) {
        if (fields) { Object.assign(this, fields); }
    }
}