import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Store } from '@ngrx/store';
import { max } from 'moment';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { Subscription } from 'rxjs';
import { AppState } from 'src/app/store/app.reducer';
import Swal from 'sweetalert2';
import { ArchivoEvidenciaModel } from '../../models/gestion-academica/archivo-evidencia.model';
import { CalificacionModel } from '../../models/gestion-academica/calificacion.model';
import { EvaluacionCompromisoModel } from '../../models/gestion-academica/evaluacion-compromiso.model';
import { EvaluacionDetalleModel } from '../../models/gestion-academica/evaluacion-detalle.model';
import { EvaluacionVcmDetalleModel } from '../../models/gestion-academica/evaluacion-vcm.model';
import { ArchivoEvidenciaService } from '../../services/archivo-evidencia.service';
import { CalificacionService } from '../../services/calificacion.service';
import { EvaluacionCompromisoDetalleService } from '../../services/evaluacion-compromiso-detalle.service';
import { EvaluacionCompromisoVcmService } from '../../services/evaluacion-compromiso-vcm.service';
import { EvaluacionCompromisoService } from '../../services/evaluacion-compromiso.service';
import { UtilAlert } from '../../util/util.alert';
import { UtilValidation } from '../../util/util.validation';

@Component({
  selector: 'app-modal-evaluacion',
  templateUrl: './modal-evaluacion.component.html',
  styles: [
  ]
})
export class ModalEvaluacionComponent implements OnInit {

  @BlockUI() blockUI: NgBlockUI;

  @ViewChild('modalEvaluacion', { static: false })
  modalEvaluacion: ElementRef;


  public listadoCalificaciones: CalificacionModel[] = []
  public evaluacion: EvaluacionCompromisoModel;
  public respuestas: EvaluacionDetalleModel[];
  public respuestasVcm: EvaluacionVcmDetalleModel[];

  public listadoPreguntasDocencia: EvaluacionDetalleModel[] = [];
  public listadoPreguntasInvestigacion: EvaluacionDetalleModel[] = [];
  public listadoPreguntasGestion: EvaluacionDetalleModel[] = [];
  public listadoPreguntasVcm: EvaluacionDetalleModel[] = [];

  public puntajePosible: number;
  public puntajeObtenido: number;

  public puntajeObtenidoDocencia: number;
  public puntajeObtenidoInvestigacion: number;
  public puntajeObtenidoGestion: number;
  public puntajeObtenidoVcm: number;

  public puntajeMaximoDocencia: number;
  public puntajeMaximoInvestigacion: number;
  public puntajeMaximoGestion: number;
  public puntajeMaximoVcm: number;

  public puntajeObtenidoFuncion: number;
  public puntajeMaximoFuncion: number;

  public recomendacionSistema: number;

  public porcentajeDocencia: number;
  public porcentajeInvestigacion: number;
  public porcentajeGestion: number;
  public porcentajeVcm: number;

  public formularioEvaluacion: FormGroup;

  public archivo: ArchivoEvidenciaModel[] = [];


  constructor(
    private modalParaEvaluacion: NgbModal,
    private store: Store<AppState>,
    private utilValidation: UtilValidation,
    public alertService: UtilAlert,
    private toastr: ToastrService,
    private calificacionService: CalificacionService,
    private evaluacionCompromisoService: EvaluacionCompromisoService,
    private evaluacionDetalleService: EvaluacionCompromisoDetalleService,
    private evaluacionVcmService: EvaluacionCompromisoVcmService,
    private archivoService: ArchivoEvidenciaService,
    private router: Router,
  ) { }

  ngOnInit(): void {
    this.traerCalificaciones();
    this.formularioEvaluacion = new FormGroup({
      nota: new FormControl("", [Validators.required])
    })

  }

  openModal(evaluacion: EvaluacionCompromisoModel, respuestas: EvaluacionDetalleModel[], respuestasVcm: EvaluacionVcmDetalleModel[], archivo: ArchivoEvidenciaModel[]) {
    this.evaluacion = { ...evaluacion };
    this.archivo = archivo;
    this.respuestas = respuestas;
    this.respuestasVcm = respuestasVcm;
    this.porcentajeDocencia = (evaluacion.idCompromiso.porcentajeDocencia) / 100
    this.porcentajeInvestigacion = (evaluacion.idCompromiso.porcentajeInvestigacion) / 100
    this.porcentajeGestion = (evaluacion.idCompromiso.porcentajeGestion) / 100
    this.porcentajeVcm = (evaluacion.idCompromiso.porcentajeVcm) / 100
    this.modalParaEvaluacion.open(this.modalEvaluacion, { size: 'sm', ariaLabelledBy: 'modal-basic-title', centered: true }).result.then(() => this.cerrarModal(false), () => this.cerrarModal(false));
    this.filtrarPorTipo();
    this.calcularPorFuncion();
    this.traerCalificaciones()
    console.log(this.archivo);
  }

  traerCalificaciones() {
    if (this.listadoCalificaciones.length == 0) {
      this.calificacionService.getCalificaciones().subscribe(value => {
        this.listadoCalificaciones = value;
      })
    } else {
      this.recomendacionPuntaje();
    }
  }

  filtrarPorTipo() {
    this.listadoPreguntasDocencia = []
    this.listadoPreguntasGestion = []
    this.listadoPreguntasInvestigacion = []
    this.listadoPreguntasVcm = []
    let puntajeMaximoDocencia = 0;
    let puntajeMaximoVcm = 0;
    let puntajeMaximoInvestigacion = 0;
    let puntajeMaximoGestion = 0;
    let puntajeObtenidoDocencia: number = 0;
    let puntajeObtenidoInvestigacion: number = 0;
    let puntajeObtenidoGestion: number = 0;
    let puntajeObtenidoVcm: number = 0;
    this.respuestas.forEach(respuesta => {
      switch (respuesta.idPregunta.idTipoPregunta.id) {
        case 0:
          this.listadoPreguntasDocencia.push(respuesta)
          if (respuesta.respuesta != 'NoAplica') { puntajeMaximoDocencia += 1 }
          if (respuesta.respuesta == "SI") { puntajeObtenidoDocencia += 1 }
          break;
        case 1:
          this.listadoPreguntasInvestigacion.push(respuesta)
          if (respuesta.respuesta != 'NoAplica') { puntajeMaximoInvestigacion += 1 }
          if (respuesta.respuesta == "SI") { puntajeObtenidoInvestigacion += 1 }
          break;
        case 2:
          this.listadoPreguntasGestion.push(respuesta)
          if (respuesta.respuesta != 'NoAplica') { puntajeMaximoGestion += 1 }
          if (respuesta.respuesta == "SI") { puntajeObtenidoGestion += 1 }
          break;
        case 3:
          this.listadoPreguntasVcm.push(respuesta)
          if (respuesta.respuesta != 'NoAplica') { puntajeMaximoVcm += 1 }
          if (respuesta.respuesta == "SI") { puntajeObtenidoVcm += 1 }
          break;
      }
    })
    this.puntajeMaximoDocencia = puntajeMaximoDocencia;
    this.puntajeMaximoInvestigacion = puntajeMaximoInvestigacion;
    this.puntajeMaximoGestion = puntajeMaximoGestion;
    this.puntajeMaximoVcm = puntajeMaximoVcm;
    this.puntajeObtenidoDocencia = puntajeObtenidoDocencia;
    this.puntajeObtenidoInvestigacion = puntajeObtenidoInvestigacion;
    this.puntajeObtenidoGestion = puntajeObtenidoGestion;
    this.puntajeObtenidoVcm = puntajeObtenidoVcm;
    this.puntajePosible = puntajeMaximoDocencia + puntajeMaximoGestion + puntajeMaximoInvestigacion + puntajeMaximoVcm;
    this.puntajeObtenido = puntajeObtenidoDocencia + puntajeObtenidoGestion + puntajeObtenidoInvestigacion + puntajeObtenidoVcm;
  }

  calcularPorFuncion() {
    var puntajeObtenidoFuncion = 0;
    var puntajeMaximoFuncion = 0;
    this.respuestasVcm.forEach(respuesta => {
      if (respuesta.respuesta == "SI") {
        puntajeObtenidoFuncion += 1
      }
      puntajeMaximoFuncion += 1
    })

    this.puntajeMaximoFuncion = puntajeMaximoFuncion;
    this.puntajeObtenidoFuncion = puntajeObtenidoFuncion;

    this.puntajeObtenido += puntajeObtenidoFuncion;
    this.puntajePosible += puntajeMaximoFuncion;
  }

  recomendacionPuntaje() {
    let valorNumero = -1;
    let porcentajePuntajeDocencia = 0;
    let porcentajePuntajeInvestigacion = 0;
    let porcentajePuntajeGestion = 0;
    let porcentajePuntajeVcm = 0;
    this.listadoCalificaciones.forEach(valor => {
      if (valorNumero < valor.calificacionNumero) {
        valorNumero = valor.calificacionNumero;
      }
    })


    porcentajePuntajeDocencia = this.puntajeMaximoDocencia != 0 ? (this.puntajeObtenidoDocencia / this.puntajeMaximoDocencia) * this.porcentajeDocencia * valorNumero : 0;
    porcentajePuntajeInvestigacion = this.puntajeMaximoInvestigacion != 0 ? (this.puntajeObtenidoInvestigacion / this.puntajeMaximoInvestigacion) * this.porcentajeInvestigacion * valorNumero : 0;
    porcentajePuntajeGestion = this.puntajeMaximoGestion != 0 ? (this.puntajeObtenidoGestion / this.puntajeMaximoGestion) * this.porcentajeGestion * valorNumero : 0;
    porcentajePuntajeVcm = this.puntajeMaximoVcm != 0 ? (this.puntajeObtenidoVcm / this.puntajeMaximoVcm) * this.porcentajeVcm * valorNumero : 0;
    this.recomendacionSistema = Math.round(porcentajePuntajeDocencia + porcentajePuntajeGestion + porcentajePuntajeInvestigacion + porcentajePuntajeVcm);
    if (this.recomendacionSistema < 1) {
      this.recomendacionSistema = 1
    }
  }

  guardarEvaluacion() {
    if (this.formularioEvaluacion.invalid) {
      this.toastr.warning("Ingrese una nota para el compromiso")
      return;
    }
    Swal.fire({
      title: 'Desea finalizar con la evaluacin?',
      text: "Esta accin no se puede deshacer",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Aceptar',
      reverseButtons: true,
      cancelButtonText: 'Cancelar',
    }).then((result) => {
      if (result.isConfirmed) {
        this.procesoGuardado();
      }
    })

  }

  procesoGuardado() {
    let calificacion: CalificacionModel = new CalificacionModel();
    calificacion.idCalificacion = this.formularioEvaluacion.get("nota").value;
    this.evaluacion.idCalificacion = calificacion;
    this.evaluacionCompromisoService.save(this.evaluacion).subscribe((value) => {
      this.archivo.forEach(dato => dato.idEvaluacion = value);
      this.archivoService.save(this.archivo).subscribe();
      this.respuestas.forEach(respuesta => respuesta.idEvaluacion = value)
      this.evaluacionDetalleService.save(this.respuestas).subscribe();
      if (this.respuestasVcm.length != 0) {
        this.respuestasVcm.forEach(respuesta => respuesta.idEvaluacion = value)
        this.evaluacionVcmService.save(this.respuestasVcm).subscribe();
      }
      this.alertService.successSwal(
        `Se ha registrado correctamente la evaluacion`
      );
      this.cerrarModal();
      this.router.navigate(["./compromisos/compromiso-list"])


    })
  }



  cerrarModal(dimissAll = true) {
    if (dimissAll) this.modalParaEvaluacion.dismissAll();
  }
}