import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';
import { SubjerarquiaModel } from '../models/gestion-academica/subjerarquia.model';
import { RequerimientosEspecialesModel } from '../models/gestion-academica/requerimientos-especiales.model';
@Injectable({
    providedIn: 'root'
})
export class RequerimientosEspecialesService {

    constructor(
        private http: HttpClient,
    ) { }

    public getByCompromisoId(compromisoId: number): Observable<RequerimientosEspecialesModel[]> {
        return this.http.get<RequerimientosEspecialesModel[]>(`${environment.backend.gestion_academica}api/requerimientos-especiales/v1/get-by-compromiso/${compromisoId}`);
    }

    public save(requerimientosEspeciales: Array<RequerimientosEspecialesModel>, compromisoId: number): Observable<Array<RequerimientosEspecialesModel>> {
        return this.http.post<Array<RequerimientosEspecialesModel>>(`${environment.backend.gestion_academica}api/requerimientos-especiales/${compromisoId}`, requerimientosEspeciales);
    }


}