import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CompromisoFormComponent } from './compromiso-form.component';


const routes: Routes = [
  { path: '', component: CompromisoFormComponent },
  { path: 'view', component: CompromisoFormComponent },
  { path: 'evaluate', component: CompromisoFormComponent }
];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class CompromisoFormRoutingModule { }
